body {
  text-rendering: optimizeSpeed;
  font: 24px "Montserrat", sans-serif;
  font-weight: 600;
  color: #fff;
  min-width: 320px;
  background-color: #000; }

.wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative; }
  .wrapper:before {
    content: '';
    display: block;
    position: absolute;
    z-index: 1;
    width: 100%;
    height: 100%;
    top: 0;
    background: url("../img/image.png") no-repeat center center;
    background-size: cover; }

.main {
  position: relative;
  z-index: 2;
  width: 100%;
  padding: 60px 30px; }
  .main__title {
    font-size: 48px;
    display: block;
    text-align: center;
    text-transform: uppercase;
    margin: 0; }

.logo {
  width: 268px;
  margin: 0 auto;
  position: relative;
  padding-bottom: 50px;
  margin-bottom: 45px; }
  .logo:after {
    content: '';
    display: block;
    width: 150px;
    height: 1px;
    background-color: #fff;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%); }
  .logo__link {
    display: block;
    width: 100%; }
  .logo__image {
    display: block;
    width: 100%; }

.contacts {
  padding-top: 94px;
  margin: 0 auto;
  max-width: 466px;
  font-weight: 600;
  text-align: center; }
  .contacts__title {
    text-transform: uppercase;
    margin: 0;
    padding-bottom: 17px;
    font-size: 24px; }
  .contacts__list {
    list-style: none;
    padding: 0;
    margin: 0; }
  .contacts__item:not(:last-child) {
    padding-bottom: 23px; }
  .contacts__link {
    text-decoration: none;
    color: #fff;
    padding: 26px 0;
    display: block;
    width: 100%;
    text-align: center;
    border-radius: 4px;
    background-color: rgba(255, 255, 255, 0.1);
    transition: 0.3s; }
    .contacts__link:hover {
      background-color: rgba(255, 255, 255, 0.4); }

@media all and (min-width: 992px) and (max-height: 700px) {
  .contacts {
    padding-top: 50px; }
  .logo {
    margin-bottom: 30px;
    padding-bottom: 40px; }
  .main {
    padding: 40px 30px; } }

@media all and (max-width: 576px) {
  .logo {
    width: 217px; }
  .main__title {
    font-size: 36px; }
  .contacts {
    padding-top: 60px; }
  .contacts__link {
    font-size: 18px; }
  .main {
    padding: 40px 30px; } }

@media all and (max-width: 340px) {
  .main {
    padding: 40px 15px; } }
